<template>
    <section>

        <div style="margin: 15px 0;">
            <el-row>
                <el-radio-group @change="regionChange" v-model="curRegion">
                    <el-radio-button label="">全市</el-radio-button>
                    <el-radio-button label="宝安"></el-radio-button>
                    <el-radio-button label="福田"></el-radio-button>
                    <el-radio-button label="龙岗"></el-radio-button>
                    <el-radio-button label="罗湖"></el-radio-button>
                    <el-radio-button label="南山"></el-radio-button>
                    <el-radio-button label="盐田"></el-radio-button>
                </el-radio-group>
            </el-row>

            <el-row style="margin: 15px 0;display: flex;">
                <el-input class="input-search" v-model="key" placeholder='输入关键字'
                          @keyup.enter.native="getHouseList">
                    <i slot="prefix" class="el-input__icon el-icon-search"></i>
                    <el-button slot="append" icon="el-icon-search" @click="getHouseList">搜索</el-button>

                </el-input>
            </el-row>
        </div>

        <el-table v-loading="listLoading"
                  :data="houseList"
                  border
                  style="width: 100%;margin-top: 20px;margin-bottom: 15px;">
            <el-table-column prop="projectName" label="项目名称">
                <template slot-scope="scope">
                    <el-button type="text" @click="jumpHouse(scope.row)">{{scope.row.projectName}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="regionName" width="120" label="区域"></el-table-column>
            <el-table-column prop="houseNum" width="130" label="房屋编码"></el-table-column>
            <el-table-column width="130" prop="useType" label="房屋用途"></el-table-column>
            <el-table-column prop="buildArea" width="100" label="建筑面积"></el-table-column>
            <el-table-column prop="expectPrice" width="110" label="期望总价(万)"></el-table-column>
            <el-table-column prop="unitPrice" width="110"  label="期望单价(万)"></el-table-column>
            <el-table-column prop="publishDate" :formatter="formatDate" label="发布日期"></el-table-column>

        </el-table>
        <div v-if="houseList && houseList.length>0" class="block"
             style="float: right; margin: 10px 0;">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20, 30, 50]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next"
                    :total="totalNum">
            </el-pagination>
        </div>
    </section>
</template>

<script>
    import util from '../../common/js/util'
    export default {
        name: "SzSecondHandList",
        data() {
            return {
                curRegion:'',
                houseList: [],
                key: "",
                listLoading: false,
                pageSize: 10,
                totalNum: 0,
                currentPage: 1,
            }
        },
        methods: {
            formatDate(row, column) {
                return util.formatDate.format(new Date(row.publishDate),
                    'yyyy-MM-dd');
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getHouseList();
            },
            handleSizeChange(size) {
                this.currentPage = 1;
                this.pageSize = size;
                this.getHouseList()
            },
            regionChange(region){
                this.curRegion=region;
                this.getHouseList();
            },
            jumpHouse(row){
                this.$router.push({
                    path: '/SzSecondHandInfo',
                    query: {
                        houseNum: row.houseNum
                    }
                })
            },
            getHouseList() {
                this.listLoading = true;
                this.httpGet('/sz/web/second/hand/querySecondHandList', {
                    key: this.key,
                    regionName: this.curRegion,
                    pageSize: this.pageSize,
                    curPage: this.currentPage,
                }).then(data => {
                    if (data.code === 0) {
                        this.houseList = data.feeds.list;
                        this.totalNum = data.feeds.totalItems;
                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                    this.listLoading = false;

                })
            }
        },
        mounted() {
            this.getHouseList();
        }
    }
</script>

<style scoped>

</style>


