// 配置API接口地址
var baseUrl = process.env.URL_SCHEME + process.env.URL_HOST;
var developer = process.env.DEVELOPER;

// 引用axios
import axios from 'axios';
axios.defaults.timeout = 100000;
axios.defaults.baseURL =baseUrl;


/**
 * 封装post请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function kmoviePost(url,data = {},contentType){
    let token = sessionStorage.getItem("token");
    let userId = sessionStorage.getItem("userId");
    if (token){
        url += url.indexOf("?") !== -1 ? "&token="+token+"&userId="+userId : "?token="+token+"&userId="+userId;
    }
    // var header = {
    //     headers: {'Content-Type': 'application/json;charset=utf-8'}
    // };
    var header={
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
    };
    const encodeForm = (data) => {
        return Object.keys(data)
            .map(key => encodeURIComponent(key) + '=' + encodeURIComponent(data[key]))
            .join('&');
    }
    return new Promise((resolve,reject) => {
        axios.post(url,encodeForm(data),header)
            .then(response => {
                resolve(response.data);
            },err => {
                reject(err)
            })
    })
}


/**
 * 封装put请求
 * @param url
 * @returns {Promise}
 */

export function kmovieGet(url, param = {}){
    let token = sessionStorage.getItem("token");
    let userId = sessionStorage.getItem("userId");
    if (token){
        url += url.indexOf("?") !== -1 ? "&token="+token+"&userId="+userId : "?token="+token+"&userId="+userId;
    }
    return new Promise((resolve,reject) => {
        axios.get(url, {params:param})
            .then(response => {
                resolve(response.data);
            },err => {
                reject(err)
            })
    })
}





