<template>
    <section>
        <div class="project-info">
            <h4>{{project.name}}</h4>
            <p><i class="fa fa-paw" aria-hidden="true"></i> <strong style="color: #286090">区域：</strong>{{project.region}}
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<i
                        class="fa fa-paw" aria-hidden="true"></i><strong style="color: #286090">
                    预售总套数：</strong>{{project.sumCount}} &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<i class="fa fa-paw"
                                                                                         aria-hidden="true"></i><strong
                        style="color: #286090"> 已售总套数：</strong>{{project.signedCount}}
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<i class="fa fa-paw" aria-hidden="true"></i><strong
                        style="color: #286090"> 待售总套数：</strong>{{project.signCount}}
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<i class="fa fa-paw" aria-hidden="true"></i> <strong
                        style="color: #286090">地址：</strong>{{project.address}}
            </p>
        </div>

        <div>
            <div style="margin: 17px 15px;display: grid;">
                <div v-for="item in project.useTypes">
                    <el-col :span="2">
                        <div><i class="fa fa-home" aria-hidden="true"></i>用途: <span style="color: #20a0ff">{{item.useType}}</span>
                        </div>
                    </el-col>
                    <el-col :span="2">
                        <div>总套数: <span style="color: #20a0ff">{{item.sumCount}}</span>套</div>
                    </el-col>
                    <el-col :span="2">
                        <div>已售: <span style="color: #20a0ff">{{item.signedCount}}</span>套</div>
                    </el-col>
                    <el-col :span="4">
                        <div>均价: <span style="color: #20a0ff">{{item.averagePrice.toFixed(2)}}</span>元/平米</div>
                    </el-col>
                </div>
            </div>
            <div>
                <v-chart class="echarts" :theme="chartsTheme" :options="charts"/>
            </div>
        </div>

        <div style="margin-top: 30px">
            <el-button @click="downloadData"
                       type="primary" size="mini">数据下载
            </el-button>
        </div>

        <el-table v-loading="houseLoading"
                  :data="houseList"
                  border
                  style="width: 100%;margin-top: 20px;margin-bottom: 15px;">
            <el-table-column prop="name" label="房号"></el-table-column>
            <el-table-column prop="vc" label="楼栋名"></el-table-column>
            <el-table-column prop="floorNum" label="楼层"></el-table-column>
            <el-table-column width="150" prop="useType"
                             label="房屋用途"></el-table-column>
            <el-table-column width="130" prop="buildArea"
                             label="建筑面积"></el-table-column>
            <el-table-column width="130" prop="indoorArea"
                             label="室内面积"></el-table-column>
            <el-table-column width="120" prop="beiAnPrice"
                             label="单价"></el-table-column>
            <el-table-column width="120" prop="beiAnAllPrice"
                             label="总价"></el-table-column>
            <el-table-column width="150" sortable="custom" prop="state"
                             label="状态">
                <template slot-scope="scope">
                    <span type="text">{{stateList[scope.row.state-1]}}</span>
                </template>
            </el-table-column>
        </el-table>

        <div v-if="houseList && houseList.length>0" class="block"
             style="float: right; margin: 10px 0;">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20, 30, 50]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next"
                    :total="totalNum">
            </el-pagination>
        </div>
        <a :href="xlsUrl" download id="downloadData" style="display: none" target="_blank"></a>
    </section>
</template>

<script>
    import ECharts from 'vue-echarts'
    import 'echarts/lib/chart/line'
    import 'echarts/lib/component/polar'
    import vintage from '../../common/js/vintage'

    export default {
        name: "PriceBureauHouse",
        components: {
            'v-chart': ECharts
        },
        data() {
            return {
                projectId: 0,
                xlsUrl: '',
                houseList: [],
                project: {
                    sumCount: 0,
                    address: '',
                    useTypes: [],
                    signCount: 0,
                    signedCount: 0,
                    name: '',
                    region: '',
                    averagePrice: 0,
                },
                houseLoading: false,
                pageSize: 10,
                totalNum: 0,
                currentPage: 1,
                stateList: ['待售', '已售未提交备案', '已售已提交备案', '未知'],
                chartsTheme: 'vintage',
                charts: {
                    title: {
                        subtext: '来自住建局'
                    },
                    tooltip: {
                        trigger: 'axis'
                    },
                    legend: {
                        data: ['备案价格']
                    },
                    toolbox: {
                        show: true,
                        feature: {
                            dataView: {show: true, readOnly: false},
                            magicType: {show: true, type: ['line', 'bar']},
                            restore: {show: true},
                            saveAsImage: {show: true}
                        }
                    },
                    calculable: true,
                    xAxis: [
                        {
                            type: 'category',
                            boundaryGap: true,
                            data: []

                        }
                    ],
                    yAxis: [
                        {
                            type: 'value',
                            axisLabel: {
                                formatter: '{value} 元/平米'
                            }
                        },
                        {
                            name: "建筑面积",
                            type: 'value',
                            axisLabel: {
                                formatter: '{value} 平米'
                            }
                        }
                    ],
                    series: [
                        {
                            name: '备案价格',
                            type: 'line',
                            xAxisIndex: 0,
                            yAxisIndex: 0,
                            data: []
                        }, {
                            name: '建筑面积',
                            type: 'bar',
                            xAxisIndex: 0,
                            yAxisIndex: 1,
                            data: []
                        }
                    ]
                },
            }
        },
        methods: {
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getHouseList();
            },
            downloadData() {

                var downloadLink = document.getElementById("downloadData");
                let token = sessionStorage.getItem("token");
                let userId = sessionStorage.getItem("userId");
                downloadLink.href = `${process.env.URL_SCHEME}${process.env.URL_HOST}/dg/web/preSell/downloadData?projectId=` + this.projectId + "&projectName=" + this.project.name + "&token="+token+"&userId="+userId;
                downloadLink.click();
                this.$message({
                    type: 'success',
                    message: '下载成功',
                });
            },
            handleSizeChange(size) {
                this.currentPage = 1;
                this.pageSize = size;
                this.getHouseList()
            },
            getChartData() {
                this.httpGet('/dg/web/preSell/getBeiAnPriceChartData', {
                    projectId: this.projectId
                }).then(data => {
                    if (data.code === 0) {
                        this.charts.series[0].data = data.feeds.housePrice
                        this.charts.series[1].data = data.feeds.buildArea
                        this.charts.xAxis[0].data = data.feeds.houseName
                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                })
            },
            getProjectInfo() {
                this.httpGet('/dg/web/preSell/getDGProjectInfo', {
                    projectId: this.projectId
                }).then(data => {
                    if (data.code === 0) {
                        this.project = data.feeds
                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                })
            },
            getHouseList() {
                this.houseLoading = true;
                this.httpGet('/dg/web/preSell/getDGHouseList', {
                    projectId: this.projectId,
                    pageSize: this.pageSize,
                    curPage: this.currentPage,
                }).then(data => {
                    if (data.code === 0) {
                        this.houseList = data.feeds.list;
                        this.totalNum = data.feeds.totalItems;
                        this.houseLoading = false;
                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                })
            }
        },
        mounted() {
            this.getProjectInfo();
            this.getHouseList();
            this.getChartData();
        },
        created() {
            this.projectId = this.$route.query.projectId;
        }
    }
</script>

<style scoped>
    .project-info {
        padding: 20px;
        margin: 20px 0;
        border: 1px solid #eee;
        border-left-color: #1b809e;
        border-left-width: 5px;
        border-radius: 3px;
        box-sizing: border-box;
    }

    .echarts {
        width: 100%;
        height: 500px;
    }
</style>


