<template>
    <!--    每日成交数据-->
    <div>
        <div  class="block">
            <span class="demonstration">{{curDeal.newHouse.statisticsTime}} 新房</span>
            <el-table
                    :data="curDeal.newHouse.data"
                    stripe
                   >
                <el-table-column
                        prop="region"
                        label="区域">
                </el-table-column>
                <el-table-column
                        prop="soldArea"
                        label="成交面积">
                </el-table-column>
                <el-table-column
                        prop="soldCount"
                        label="成交套数">
                </el-table-column>
                <el-table-column
                        prop="soldPrice"
                        label="成交单价">
                </el-table-column>
            </el-table>
        </div>
        <div  class="block">
            <span class="demonstration">{{curDeal.secondHouse.statisticsTime}} 二手房</span>
            <el-table
                    :data="curDeal.secondHouse.data"
                    stripe>
                <el-table-column
                        prop="region"
                        label="区域">
                </el-table-column>
                <el-table-column
                        prop="soldArea"
                        label="成交面积">
                </el-table-column>
                <el-table-column
                        prop="soldCount"
                        label="成交套数">
                </el-table-column>
                <el-table-column
                        prop="newCount"
                        label="新增套数">
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
    import util from '../../common/js/util'

    export default {
        name: "DealData",
        data() {
            return {
                curDeal: {
                    newHouse: {
                        statisticsTime:'',
                        data: [],
                        loading: false
                    },
                    secondHouse: {
                        data: [],
                        statisticsTime:'',
                        loading: false
                    }
                }
            }
        },
        methods: {
            getCurNewHouseDeal() {
                this.curDeal.newHouse.loading = true;
                this.httpGet('/sz/web/day/deal/queryNewHouseRegion', {}).then(data => {
                    if (data.code === 0) {
                        this.curDeal.newHouse.data = data.feeds;
                        this.curDeal.newHouse.statisticsTime=util.formatDate
                            .format(new Date(data.feeds[0].staticDate),
                            'yyyy-MM-dd')
                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                    this.curDeal.newHouse.loading = false;
                })
            },
            getCurSecondHouseDeal() {
                this.curDeal.secondHouse.loading = true;
                this.httpGet('/sz/web/day/deal/querySHHouseRegion', {}).then(data => {
                    if (data.code === 0) {
                        this.curDeal.secondHouse.data = data.feeds;
                        this.curDeal.secondHouse.statisticsTime=util.formatDate
                            .format(new Date(data.feeds[0].staticDate),
                                'yyyy-MM-dd')
                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                    this.curDeal.secondHouse.loading = false;
                })
            }
        },
        mounted() {
            this.getCurNewHouseDeal();
            this.getCurSecondHouseDeal();
        }
    }
</script>

<style scoped>
    .block {
        padding: 30px;
        text-align: center;
        border-right: 1px solid #eff2f6;
        display: inline-grid;
        width: 49%;
        box-sizing: border-box
    }
</style>


