<template>
    <section>

        <div style="margin: 15px 0;">
            <el-row>
                <el-input class="input-search" v-model="key" placeholder='项目名'
                          @keyup.enter.native="getProjectList">
                    <i slot="prefix" class="el-input__icon el-icon-search"></i>
                </el-input>
            </el-row>
        </div>

        <el-table v-loading="projectLoading"
                  :data="projectList"
                  border
                  style="width: 100%;margin-top: 20px;margin-bottom: 15px;">
            <el-table-column prop="openDate" :formatter="formatDate" label="开盘日期"></el-table-column>
            <el-table-column prop="name" label="项目名">
                <template slot-scope="scope">
                    <el-button type="text" @click="jumpProjectHouse(scope.row)">{{scope.row.name}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="region" label="区域"></el-table-column>
            <el-table-column prop="address" label="地址"></el-table-column>

        </el-table>
        <div v-if="projectList && projectList.length>0" class="block"
             style="float: right; margin: 10px 0;">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20, 30, 50]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next"
                    :total="totalNum">
            </el-pagination>
        </div>
    </section>
</template>

<script>
    import util from '../../common/js/util'
    export default {
        name: "ReadyProject",
        data() {
            return {
                projectList: [],
                key: "",
                projectLoading: false,
                pageSize: 10,
                totalNum: 0,
                currentPage: 1,
            }
        },
        methods: {
            formatDate(row, column) {
                return util.formatDate.format(new Date(row.openDate),
                    'yyyy-MM-dd');
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getProjectList();
            },
            handleSizeChange(size) {
                this.currentPage = 1;
                this.pageSize = size;
                this.getProjectList()
            },
            jumpProjectHouse(row){
                this.$router.push({
                    path: '/DgNewProjectHouse',
                    query: {
                        projectId: row.id,
                    }
                })
            },
            getProjectList() {
                this.projectLoading = true;
                this.httpGet('/dg/web/preSell/getDGProjectList', {
                    key: this.key,
                    pageSize: this.pageSize,
                    curPage: this.currentPage,
                }).then(data => {
                    if (data.code === 0) {
                        this.projectList = data.feeds.list;
                        this.totalNum = data.feeds.totalItems;
                        this.projectLoading = false;
                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                })
            }
        },
        mounted() {
            this.getProjectList();
        }
    }
</script>

<style scoped>

</style>


