<template>
    <section>
        <div style="margin-top: 15px">
            <el-row>
                <div style="display: inline;margin-left: 25px">项目名:
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                </div>
                <span>{{project.projectName}}</span>
            </el-row>
        </div>

        <div class="project-info">
            <el-row :gutter="20" v-for="(item,index) in averagePrice"
                    v-bind:key="item.useType">
                <el-col :span="4">
                    <div>
                        用途：<span style="color: #20a0ff">{{item.useType}}</span>
                    </div>
                </el-col>
                <el-col :span="5">
                    <div>均价: <span style="color: #20a0ff">{{item.unitPrice.toFixed(2)}}</span>元/平米</div>
                </el-col>
                <el-col :span="5">
                    <div>套数: <span style="color: #20a0ff">{{item.state}}</span>套</div>
                </el-col>
                <el-col :span="4" v-if="index===averagePrice.length-1">
                    <el-button @click="downloadData"
                               type="primary" size="mini">数据下载
                    </el-button>
                </el-col>
            </el-row>
        </div>
        <el-tabs v-model="selectStatus" type="card" @tab-click="handleClick">

            <el-tab-pane v-if="stateCount.signCount>0" name="signCount">
                <span slot="label"> 待售 <el-button type="danger" size="mini"
                                                  round>{{stateCount.signCount}}</el-button></span>
                <el-table v-loading="houseLoading"
                          :data="houseList"
                          @sort-change="sortChange"
                          border
                          style="width: 100%;margin-top: 20px;margin-bottom: 15px;">
                    <el-table-column prop="updateDate" sortable="custom" label="上架时间" width="110">
                        <template slot-scope="scope">
                            <span type="text">{{scope.row.sellTime | formatDate}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="houseNo" label="房号"></el-table-column>
                    <el-table-column sortable="custom" prop="useType" label="房屋用途"></el-table-column>
                    <el-table-column width="110" sortable="custom" prop="buildArea"
                                     label="建筑面积"></el-table-column>
                    <el-table-column width="110" sortable="custom" prop="unitPrice"
                                     label="单价"></el-table-column>
                    <el-table-column width="110" sortable="custom" prop="sumPrice"
                                     label="总价"></el-table-column>
                    <el-table-column width="80" sortable="custom" prop="state"
                                     label="状态">
                        <template slot-scope="scope">
                            <div>
                                {{scope.row.state===1?'待售':'已售'}}
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column prop="owner"
                                     label="开发商"></el-table-column>
                    <el-table-column width="150" prop="landNum"
                                     label="地块"></el-table-column>
                </el-table>
            </el-tab-pane>
            <el-tab-pane v-if="stateCount.signedCount>0" name="signedCount">
                <span slot="label"> 已售 <el-button type="danger" size="mini"
                                                  round>{{stateCount.signedCount}}</el-button></span>
                <el-table v-loading="houseLoading"
                          :data="houseList"
                          @sort-change="sortChange"
                          border
                          style="width: 100%;margin-top: 20px;margin-bottom: 15px;">
                    <el-table-column prop="updateDate" label="签约时间" sortable="custom" width="110">
                        <template slot-scope="scope">
                            <span type="text">{{scope.row.sellTime | formatDate}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="houseNo" label="房号"></el-table-column>
                    <el-table-column sortable="custom" prop="useType" label="房屋用途"></el-table-column>
                    <el-table-column width="130" sortable="custom" prop="buildArea"
                                     label="建筑面积"></el-table-column>
                    <el-table-column width="130" sortable="custom" prop="unitPrice"
                                     label="单价"></el-table-column>
                    <el-table-column width="130" sortable="custom" prop="sumPrice"
                                     label="总价"></el-table-column>
                    <el-table-column width="80" sortable="custom" prop="state"
                                     label="状态">
                        <template slot-scope="scope">
                            <div>
                                {{scope.row.state===1?'待售':'已售'}}
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column prop="owner"
                                     label="开发商"></el-table-column>
                    <el-table-column width="150" prop="landNum"
                                     label="地块"></el-table-column>
                </el-table>
            </el-tab-pane>
        </el-tabs>
        <div v-if="houseList && houseList.length>0" class="block"
             style="float: right; margin: 10px 0;">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20, 30, 50]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next"
                    :total="totalNum">
            </el-pagination>
        </div>
        <a :href="xlsUrl" download id="downloadData" style="display: none" target="_blank"></a>

    </section>
</template>

<script>
    import util from '../../common/js/util'

    export default {
        name: "ReadyProjectHouse",
        data() {
            return {
                xlsUrl: '',
                houseList: [],
                project: {
                    id: 0,
                    projectName: '',
                    permitNum: '',
                    companyName: '',
                    address: ''
                },
                stateCount: {
                    signedCount: 0,
                    signCount: 0
                },
                selectStatus: "signCount",
                houseStatus: 1,
                sortColumn: 'use_type',
                sortOrder: 'asc',
                projectId: 0,
                houseLoading: false,
                pageSize: 10,
                totalNum: 0,
                currentPage: 1,
                averagePrice: []
            }
        },
        filters: {
            formatDate(time) {
                if (time < 1) {
                    return "未知";
                }
                return util.formatDate.format(new Date(time),
                    'yyyy-MM-dd');
            },
        },
        methods: {
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getHouseList();
            },
            handleClick() {
                this.currentPage = 1;
                this.sortColumn = "sell_time";
                this.sortOrder = 'asc';
                switch (this.selectStatus) {
                    case "signCount":
                        this.houseStatus = 1;
                        this.sortColumn = "sell_time";
                        this.sortOrder = 'asc';
                        break;
                    case "signedCount":
                        this.sortColumn = "sell_time";
                        this.sortOrder = 'desc';
                        this.houseStatus = 2;
                        break;
                }
                this.getHouseList()
            },
            downloadData() {

                var downloadLink = document.getElementById("downloadData");
                let token = sessionStorage.getItem("token");
                let userId = sessionStorage.getItem("userId");
                downloadLink.href = `${process.env.URL_SCHEME}${process.env.URL_HOST}/sz/web/now/sell/downloadData?projectId=` + this.project.id + "&projectName=" + this.project.projectName + "&token=" + token + "&userId=" + userId;
                downloadLink.click();
                this.$message({
                    type: 'success',
                    message: '下载成功',
                });
            },
            sortChange(ele) {
                if (ele.prop === 'useType') {
                    this.sortColumn = 'use_type';
                }
                if (ele.prop === 'buildArea') {
                    this.sortColumn = 'build_area';
                }
                if (ele.prop === 'unitPrice') {
                    this.sortColumn = 'unit_price';
                }
                if (ele.prop === 'sumPrice') {
                    this.sortColumn = 'sum_price';
                }
                if (ele.prop === 'sellTime') {
                    this.sortColumn = 'sell_time';
                }
                if (ele.prop === 'state') {
                    this.sortColumn = 'state';
                }
                if (ele.order === 'descending') {
                    this.sortOrder = 'desc';
                } else {
                    this.sortOrder = 'asc';
                }
                this.currentPage = 1;
                this.getHouseList();
            },
            handleSizeChange(size) {
                this.currentPage = 1;
                this.pageSize = size;
                this.getHouseList()
            },
            getAveragePrice() {
                this.httpGet('/sz/web/now/sell/getAveragePrice', {
                    projectId: this.project.id
                }).then(data => {
                    if (data.code === 0) {
                        this.averagePrice = data.list;
                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                })
            },
            getStateCount() {
                this.httpGet('/sz/web/now/sell/getStateCount', {
                    projectId: this.project.id
                }).then(data => {
                    if (data.code === 0) {
                        this.stateCount = data.feed;
                        if (this.stateCount.signCount > 0) {
                            this.selectStatus = "signCount";
                            this.houseStatus = 1;
                        } else {
                            this.selectStatus = "signedCount";
                            this.houseStatus = 2;
                        }

                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                    this.getHouseList();
                })
            },
            getHouseList() {
                this.houseLoading = true;
                this.httpGet('/sz/web/now/sell/getHouse', {
                    projectId: this.project.id,
                    pageSize: this.pageSize,
                    state: this.houseStatus,
                    sortOrder: this.sortOrder,
                    sortColumn: this.sortColumn,
                    curPage: this.currentPage,
                }).then(data => {
                    if (data.code === 0) {
                        this.houseList = data.feeds.list;
                        this.totalNum = data.feeds.totalItems;
                        this.houseLoading = false;
                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                })
            }
        },
        mounted() {
            this.getStateCount();
            this.getAveragePrice();
        },
        created() {
            this.project.id = this.$route.query.projectId;
            this.project.projectName = this.$route.query.projectName;
        }
    }
</script>

<style scoped>
    .project-info {
        padding: 20px;
        margin: 20px 0;
        border: 1px solid #eee;
        border-left-color: #1b809e;
        border-left-width: 5px;
        border-radius: 3px;
        box-sizing: border-box;
    }
</style>


