<script>
    import ECharts from 'vue-echarts'
    import 'echarts/lib/chart/line'
    import 'echarts/lib/component/polar'
    import util from '../../common/js/util'
    import vintage from '../../common/js/vintage'

    export default {
        name: "PriceBureauHouse",
        components: {
            'v-chart': ECharts
        },
        data() {
            return {
                xlsUrl: '',
                houseList: [],
                project: {
                    id: 0,
                    projectName: '',
                    permitNum: '',
                    companyName: '',
                    permitDate: '',
                    transferDate: '',
                    projectUsePeriod: '',
                    transferNo: '',
                    projectSaleID: 0,
                    address: ''
                },
                signInfo: {
                    signedCount: 0,
                    signCount: 0,
                    anjuCount: 0,
                    autoLockCount: 0,
                    allCount: 0
                },
                selectStatus: 'allCount',
                stateList: ["期房待售", "已售", "已签预售合同", "已备案", "已签认购书", "初始登记", "管理局锁定", "自动锁定", "安居型商品房", "司法查封", "委员会锁定", "分局锁定", "未知状态"],
                sortColumn: 'price',
                sortOrder: 'asc',
                projectId: 0,
                houseLoading: false,
                pageSize: 10,
                totalNum: 0,
                currentPage: 1,
                chartsData: [],
                chartsTheme: 'vintage',
                curDealType: 'chart',
                select: {
                    useType: '',
                    state: '',
                },
                deal: {
                    charts: {
                        total: 15,
                        dealData: {
                            title: {
                                subtext: '来自住建局'
                            },
                            tooltip: {
                                trigger: 'axis'
                            },
                            legend: {
                                data: ['当日成交单价', '总成交单价', '当日成交面积']
                            },
                            toolbox: {
                                show: true,
                                feature: {
                                    dataView: {show: true, readOnly: false},
                                    magicType: {show: true, type: ['line', 'bar']},
                                    restore: {show: true},
                                    saveAsImage: {show: true}
                                }
                            },
                            calculable: true,
                            xAxis: [
                                {
                                    type: 'category',
                                    boundaryGap: true,
                                    data: []

                                }
                            ],
                            yAxis: [
                                {
                                    type: 'value',
                                    axisLabel: {
                                        formatter: '{value} 元/平米'
                                    }
                                },
                                {
                                    name: "建筑面积",
                                    type: 'value',
                                    axisLabel: {
                                        formatter: '{value} 平米'
                                    }
                                }
                            ],
                            series: [
                                {
                                    name: '当日成交单价',
                                    type: 'line',
                                    xAxisIndex: 0,
                                    yAxisIndex: 0,
                                    data: []
                                },
                                {
                                    name: '总成交单价',
                                    xAxisIndex: 0,
                                    yAxisIndex: 0,
                                    type: 'line',
                                    data: []
                                },
                                {
                                    name: '当日成交面积',
                                    xAxisIndex: 0,
                                    yAxisIndex: 1,
                                    type: 'bar',
                                    data: []
                                }
                            ]
                        },
                    },
                    table: {
                        loading: false,
                        data: [],
                        page: {
                            pageSize: 10,
                            totalNum: 0,
                            currentPage: 1,
                        }
                    }
                },
                charts: {
                    title: {
                        subtext: '来自住建局'
                    },
                    tooltip: {
                        trigger: 'axis'
                    },
                    legend: {
                        data: ['备案价格']
                    },
                    toolbox: {
                        show: true,
                        feature: {
                            dataView: {show: true, readOnly: false},
                            magicType: {show: true, type: ['line', 'bar']},
                            restore: {show: true},
                            saveAsImage: {show: true}
                        }
                    },
                    calculable: true,
                    xAxis: [
                        {
                            type: 'category',
                            boundaryGap: true,
                            data: []

                        }
                    ],
                    yAxis: [
                        {
                            type: 'value',
                            axisLabel: {
                                formatter: '{value} 元/平米'
                            }
                        },
                        {
                            name: "建筑面积",
                            type: 'value',
                            axisLabel: {
                                formatter: '{value} 平米'
                            }
                        }
                    ],
                    series: [
                        {
                            name: '备案价格',
                            type: 'line',
                            xAxisIndex: 0,
                            yAxisIndex: 0,
                            data: []
                        }, {
                            name: '建筑面积',
                            type: 'bar',
                            xAxisIndex: 0,
                            yAxisIndex: 1,
                            data: []
                        }
                    ]
                }
            }
        },
        filters: {
            formatDate(time) {
                return util.formatDate.format(new Date(time),
                    'yyyy-MM-dd');
            },
        },
        methods: {
            handleClick() {
                this.currentPage = 1;
                this.sortColumn = "buildArea";
                this.sortOrder = 'asc';
                switch (this.selectStatus) {
                    case "allCount":
                        this.select.state = '';
                        break;
                    case "signCount":
                        this.select.state = 1;
                        break;
                    case "signedCount":
                        this.sortColumn = "updateDate";
                        this.sortOrder = 'desc';
                        this.select.state = 100;
                        break;
                    case "anjuCount":
                        this.select.state = 9;
                        break;
                    case "autoLockCount":
                        this.select.state = 8;
                        break;
                }
                this.getHouseList()
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getHouseList();
            },
            handleDealTableCurrentChange(val) {
                this.deal.table.page.currentPage = val;
                this.queryDaySaleTableData();
            },
            downloadData() {

                var downloadLink = document.getElementById("downloadData");
                let token = sessionStorage.getItem("token");
                let userId = sessionStorage.getItem("userId");
                downloadLink.href = `${process.env.URL_SCHEME}${process.env.URL_HOST}/sz/web/preSell/exportProjectHouseListOpen?projectId=` + this.project.id + "&projectName=" + this.project.projectName + "&token=" + token + "&userId=" + userId;
                downloadLink.click();
                this.$message({
                    type: 'success',
                    message: '下载成功',
                });
            },
            sortChange(ele) {
                if (ele.prop === 'useType') {
                    this.sortColumn = 'useType';
                }
                if (ele.prop === 'buildArea') {
                    this.sortColumn = 'buildArea';
                }
                if (ele.prop === 'indoorArea') {
                    this.sortColumn = 'indoorArea';
                }
                if (ele.prop === 'price') {
                    this.sortColumn = 'price';
                }

                if (ele.order === 'descending') {
                    this.sortOrder = 'desc';
                } else {
                    this.sortOrder = 'asc';
                }
                this.currentPage = 1;
                this.getHouseList();
            },
            handleSizeChange(size) {
                this.currentPage = 1;
                this.pageSize = size;
                this.getHouseList()
            },

            handleDealTableSizeChange(size) {
                this.deal.table.page.currentPage = 1;
                this.deal.table.page.pageSize = size;
                this.queryDaySaleTableData()
            },
            getAveragePrice() {
                this.httpGet('/sz/web/preSell/getAveragePrice', {
                    projectId: this.project.id
                }).then(data => {
                    if (data.code === 0) {
                        for (let i = 0; i < data.list.length; i++) {
                            let cData = JSON.parse(JSON.stringify(this.charts));
                            cData.series[0].data = data.list[i].unitPrice;
                            cData.series[1].data = data.list[i].buildArea;
                            cData.xAxis[0].data = data.list[i].name;
                            this.chartsData.push({"sum": data.list[i].sum, "data": cData})
                        }
                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                })
            },
            queryDaySaleChartData() {
                this.httpGet('/sz/web/preSell/queryDaySaleChartData', {
                    total: this.deal.charts.total,
                    projectSalesID: this.project.projectSaleID
                }).then(data => {
                    if (data.code === 0) {
                        this.deal.charts.dealData.series[0].data = data.feeds.dayPrice;
                        this.deal.charts.dealData.series[1].data = data.feeds.dayPriceAll;
                        this.deal.charts.dealData.series[2].data = data.feeds.dayArea;
                        this.deal.charts.dealData.xAxis[0].data = data.feeds.time;
                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                })
            },
            getProjectDetail() {
                this.httpGet('/sz/web/preSell/queryProjectInfo', {
                    projectId: this.project.id
                }).then(data => {
                    if (data.code === 0) {
                        this.project.projectName = data.info.projectName;
                        this.project.permitNum = data.info.permitNum;
                        this.project.transferDate = data.info.transferDate;
                        this.project.permitDate = data.info.permitDate;
                        this.project.address = data.info.projectAddress;
                        this.project.projectRegion = data.info.projectRegion;
                        this.project.projectUsePeriod = data.info.projectUsePeriod;
                        this.project.companyName = data.info.developers;
                        this.project.transferNo = data.info.transferNo;
                        this.project.projectSaleID = data.info.projectSaleID;
                        if (this.project.projectSaleID > 0) {
                            this.queryDaySaleChartData();
                            this.queryDaySaleTableData();
                        }
                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                })
            },
            getSignInfo() {
                this.httpGet('/sz/web/preSell/queryHouseSignCount', {
                    projectId: this.project.id
                }).then(data => {
                    if (data.code === 0) {
                        this.signInfo.signCount = data.feeds.signCount;
                        this.signInfo.signedCount = data.feeds.signedCount;
                        this.signInfo.anjuCount = data.feeds.anjuCount;
                        this.signInfo.autoLockCount = data.feeds.autoLockCount;
                        this.signInfo.allCount = data.feeds.allCount;
                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                })
            },
            queryDaySaleTableData() {
                this.deal.table.loading = true;
                this.httpGet('/sz/web/preSell/queryDaySaleTableData', {
                    projectSalesID: this.project.projectSaleID,
                    pageSize: this.deal.table.page.pageSize,
                    curPage: this.deal.table.page.currentPage,
                }).then(data => {
                    if (data.code === 0) {
                        this.deal.table.data = data.feeds.list;
                        this.deal.table.page.totalNum = data.feeds.totalItems;
                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                    this.deal.table.loading = false;

                })
            },
            getHouseList() {
                this.houseLoading = true;
                this.httpGet('/sz/web/preSell/getHouse', {
                    projectId: this.project.id,
                    pageSize: this.pageSize,
                    sortOrder: this.sortOrder,
                    sortColumn: this.sortColumn,
                    useType: this.select.useType,
                    state: this.select.state,
                    curPage: this.currentPage,
                }).then(data => {
                    if (data.code === 0) {
                        this.houseList = data.feeds.list;
                        this.totalNum = data.feeds.totalItems;
                        this.houseLoading = false;
                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                })
            }
        },
        mounted() {
            this.getHouseList();
            this.getAveragePrice();
            this.getSignInfo();
        },
        created() {
            this.project.id = this.$route.query.projectId;
            this.getProjectDetail();
        }
    }
</script>

<style scoped>
    .echarts {
        width: 100%;
        height: 500px;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }

    .project-info {
        padding: 20px;
        margin: 20px 0;
        border: 1px solid #eee;
        border-left-color: #1b809e;
        border-left-width: 5px;
        border-radius: 3px;
        box-sizing: border-box;
    }

    .clearfix:after {
        clear: both
    }

</style>


