<template>
    <section>
        <div>
            <h3>{{info.erShou.projectName}}</h3>
            <div class="project-info">
                <p>房屋编码: {{info.erShou.houseNum}}</p>
                <p>区&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;域: {{info.erShou.regionName}}</p>
                <p>类&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;型: {{info.erShou.useType}}</p>
                <p>建筑面积: {{info.erShou.buildArea}}㎡</p>
                <p>期望总价: {{info.erShou.expectPrice}}万</p>
                <p>期望单价: {{info.erShou.unitPrice}}万</p>
                <p>发布时间: {{info.erShou.publishDate | formatDate}}</p>
            </div>
        </div>
        <el-divider></el-divider>
        <div>
            <h3>代理信息</h3>

            <div v-for="(item,index) in info.agent">
                <el-badge :value="index+1" class="item" type="primary"></el-badge>
                <div class="agent-info">
                    <p><i class="fa fa-user-circle-o" aria-hidden="true"></i>&nbsp;代理人: {{item.man.agentManName}}</p>
                    <ul class="list-group">
                        <li class="list-group-item">协议编号: {{item.man.agentNum}}</li>
                        <li class="list-group-item">联系电话: {{item.man.agentPhone}}</li>
                        <li class="list-group-item">代理时间: {{item.man.agentDate | formatDate}}</li>
                    </ul>
                    <p><i class="fa fa-users" aria-hidden="true"></i>&nbsp;代理公司: {{item.company.name}}</p>
                    <ul class="list-group">
                        <li class="list-group-item">公司法人: {{item.company.legal}}</li>
                        <li class="list-group-item">经营范围: {{item.company.businessScope}}</li>
                        <li class="list-group-item">公司地址: {{item.company.address}}</li>
                    </ul>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
    import 'echarts/lib/chart/line'
    import 'echarts/lib/component/polar'
    import util from '../../common/js/util'

    export default {
        name: "SzSecondHandInfo",
        data() {
            return {
                info: {
                    erShou: {
                        projectName: "",
                    }
                },
                houseNum: "",
            }
        },
        filters: {
            formatDate(time) {
                return util.formatDate.format(new Date(time),
                    'yyyy-MM-dd');
            },
        },
        methods: {
            getHouseInfo() {
                this.httpGet('/sz/web/second/hand/querySecondHandInfo', {
                    houseNum: this.houseNum
                }).then(data => {
                    if (data.code === 0) {
                        this.info = data.info;
                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                })
            }
        },
        created() {
            this.houseNum = this.$route.query.houseNum;
            this.getHouseInfo();
        }
    }
</script>

<style scoped>
    .project-info {
        padding: 20px;
        margin: 20px 0;
        border: 1px solid #eee;
        border-left-color: #1b809e;
        border-left-width: 5px;
        border-radius: 3px;
        box-sizing: border-box;
    }

    .list-group {
        margin-bottom: 0;
        padding-left: 0;
    }

    .agent-info {
        background-color: #fff;
        border: 1px solid #fff;
        padding: 10px 15px;
        border-left-color: #2196f3;
    }

    .list-group-item {
        position: relative;
        display: block;
        padding: 10px 15px;
        margin-bottom: -1px;
        background-color: #fff;
        border: 1px solid #ddd;
    }
</style>


