<template>
    <section>
        <el-form @keyup.enter.native="login" :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left"
                 label-width="0px" class="demo-ruleForm login-container">
            <h3 class="title">系统登录 <span style="float: right"><el-link style="color: rgb(84, 46, 46)" href="http://www.zhuzhe2.com/#/Tip" target="_blank">续上</el-link></span></h3>
            <el-form-item prop="account">
                <el-input type="text" v-model="ruleForm2.account" auto-complete="off" placeholder="手机号"></el-input>
            </el-form-item>
            <el-form-item prop="checkPass">
                <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off" placeholder="密码"></el-input>
            </el-form-item>
            <el-form-item prop="nickname" v-show="isRegister">
                <el-input type="nickname" v-model="ruleForm2.nickname" auto-complete="off" placeholder="昵称"></el-input>
            </el-form-item>
            <el-checkbox v-show="!isRegister" v-model="checked" checked class="remember">记住密码</el-checkbox>

            <el-form-item style="width:100%;">
                <el-button v-show="!isRegister" type="primary" style="width:45%;" @click="login"
                           :loading="logining">登录
                </el-button>
                <el-button type="primary" style="width:45%;float: right" @click="register()"
                           :loading="registing">注册
                </el-button>
            </el-form-item>
        </el-form>


        <footer class="footer ng-scope">
            <div class="error_ex footer-color">
                <div class="footer-default ">
                    <p>住这儿-筑这儿 &nbsp;&nbsp;&nbsp;&nbsp; <a style="color: #d0cfcf;" href="http://www.miitbeian.gov.cn/"
                                                           target="_blank">粤ICP备17096963号</a></p>
                    <p>CopyRight©2017 zhuzher Inc. 战略意向合作发邮件到zzr_ceo@163.com</p>
                    <small>本站所有内容仅供参考，不可作为任何凭证，我们不对任何内容的真实性、准确性负任何法律责任。</small>
                </div>
            </div>
        </footer>
    </section>
</template>

<script>
    //import NProgress from 'nprogress'
    import md5 from 'js-md5';

    export default {
        data() {
            return {
                logining: false,
                registing: false,
                isRegister: false,
                ruleForm2: {
                    account: '',
                    nickname: '',
                    checkPass: ''
                },
                rules2: {
                    account: [
                        {required: true, message: '请输入账号', trigger: 'blur'},
                        //{ validator: validaePass }
                    ],
                    checkPass: [
                        {required: true, message: '请输入密码', trigger: 'blur'},
                        //{ validator: validaePass2 }
                    ]
                },
                checked: true
            };
        },
        methods: {
            handleReset2() {
                this.$refs.ruleForm2.resetFields();
            },

            login(ev) {
                this.logining = true;
                let loginParams = {
                    phone: this.ruleForm2.account, password:
                        md5(this.ruleForm2.checkPass)
                };
                this.httpPost('/user/login/signIn', loginParams).then(data => {
                    this.logining = false;
                    if (data.code !== 0) {
                        this.$message({
                            message: "密码或用户名错误，请重新输入",
                            type: 'error'
                        });
                        return;
                    } else {
                        //NProgress.done()
                        sessionStorage.setItem('uesrInfo', JSON.stringify(data.info));
                        sessionStorage.setItem('token', data.token);
                        sessionStorage.setItem('userId', data.info.id);
                        this.$router.push({path: '/SzNewProject'});
                    }
                });
            },
            register(ev) {
                if (!this.isRegister) {
                    this.isRegister = true;
                    return;
                }
                this.registing = true;
                let loginParams = {
                    phone: this.ruleForm2.account, password:
                        md5(this.ruleForm2.checkPass), nickname: this.ruleForm2.nickname
                };
                this.httpPost('/user/login/register', loginParams).then(data => {
                    this.registing = false;
                    if (data.code !== 0) {

                        if (data.code === 112) {
                            this.$message({
                                message: "昵称已存在",
                                type: 'error'
                            });
                        } else if (data.code === 113) {
                            this.$message({
                                message: "手机号已存在",
                                type: 'error'
                            });
                        } else {
                            this.$message({
                                type: 'error',
                                message: '注册失败，联系' + process.env.DEVELOPER + "解决",
                            });
                        }

                        return;
                    } else {
                        //NProgress.done()
                        sessionStorage.setItem('uesrInfo', JSON.stringify(data.info));
                        sessionStorage.setItem('token', data.token);
                        sessionStorage.setItem('userId', data.info.id);
                        this.$router.push({path: '/SzNewProject'});
                    }
                });
            }
        }
    }

</script>

<style lang="scss" scoped>
    .login-container {
        /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
        -webkit-border-radius: 5px;
        border-radius: 5px;
        -moz-border-radius: 5px;
        background-clip: padding-box;
        margin: 180px auto;
        width: 350px;
        padding: 35px 35px 15px 35px;
        background: #fff;
        border: 1px solid #eaeaea;
        box-shadow: 0 0 25px #cac6c6;
    }
    .title {
      margin: 0px auto 40px auto;
      text-align: center;
      color: #505458;
    }

    .remember {
      margin: 0px 0px 35px 0px;
    }

    .footer {
      bottom: 0;
      width: 100%;
      height: 60px;
    }

    .footer-color {
      background-color: #e8e8df;
    }

    .footer-default {
      color: #999;
      font-size: 13px;
      display: inline-block;
      margin: 5px;
    }

    .error_ex {
      text-align: center;
    }
</style>


