import Home from './views/common/Home.vue'
import Login from './views/common/Login.vue'
import NotFound from './views/common/404.vue'
import Tip from './views/common/Tip.vue'

import SzPriceBureauProject from './views/sz/SzPriceBureauProject.vue'
import SzNewProject from './views/sz/SzNewProject.vue'
import SzDealData from './views/sz/SzDealData.vue'
import SzNewProjectHouse from './views/sz/SzNewProjectHouse.vue'
import SzReadyProject from './views/sz/SzReadyProject.vue'
import SzReadyProjectHouse from './views/sz/SzReadyProjectHouse.vue'
import SzPriceBureauHouse from './views/sz/SzPriceBureauHouse.vue'
import SzSecondHandList from './views/sz/SzSecondHandList.vue'
import SzSecondHandInfo from './views/sz/SzSecondHandInfo.vue'

import DgNewProject from './views/dg/DgNewProject.vue'
import DgNewProjectHouse from './views/dg/DgNewProjectHouse.vue'


let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/Tip',
        component: Tip,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: Home,
        name: '深圳',
        iconCls: 'fa fa-chrome',//图标样式class
        children: [
            { path: '/SzDealData', component: SzDealData, name: '成交统计'},
            { path: '/SzNewProject', component: SzNewProject, name: '住建局新房备案'},
            { path: '/SzNewProjectHouse', component: SzNewProjectHouse, name: '住建局新房项目',hidden:true},
            { path: '/SzReadyProject', component: SzReadyProject, name: '住建局现房备案'},
            { path: '/SzReadyProjectHouse', component: SzReadyProjectHouse, name: '住建局现房项目',hidden:true},
            { path: '/SzPriceBureauProject', component: SzPriceBureauProject, name: '物价局备案价'},
            { path: '/SzPriceBureauHouse', component: SzPriceBureauHouse, name: '物价局备案项目',hidden:true},
            { path: '/SzSecondHandList', component: SzSecondHandList, name: '二手房源'},
            { path: '/SzSecondHandInfo', component: SzSecondHandInfo, name: '二手房详情',hidden:true},



        ]
    },
    {
        path: '/',
        component: Home,
        name: '东莞',
        iconCls: 'fa fa-drupal',//图标样式class
        children: [
            { path: '/DgNewProject', component: DgNewProject, name: '新房备案'},
            { path: '/DgNewProjectHouse', component: DgNewProjectHouse, name: '新房项目',hidden:true},

        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: {path: '/404'}
    }
];

export default routes;


