<template>
    <section>
        <div class="project-info">
            <el-row>
                <div style="display: inline;margin-left: 25px">项目名:
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                </div>
                <span>{{project.projectName}}</span>
            </el-row>
            <el-row>
                <div style="display: inline;margin-left: 25px">销售许可证: &nbsp;</div>
                <span>{{project.permitNum}}</span>
            </el-row>
            <el-row>
                <div style="display: inline;margin-left: 25px">开发商: &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                </div>
                <span>{{project.companyName}}</span>
            </el-row>
            <el-row>
                <div style="display: inline;margin-left: 25px">位置: &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div>
                <span style="display: inline;">{{project.address}}</span>
            </el-row>
        </div>

        <div>

            <el-row style="margin-left: 15px;margin-top: 30px" :gutter="20" v-for="(item,index) in chartsData"
                    v-bind:key="item.sum.useType">
                <el-col :span="4">
                    <div>
                        用途：<span style="color: #20a0ff">{{item.sum.useType}}</span>
                    </div>
                </el-col>
                <el-col :span="5">
                    <div>均价: <span style="color: #20a0ff">{{item.sum.unitPrice.toFixed(2)}}</span>元/平米</div>
                </el-col>
                <el-col :span="5">
                    <div>套数: <span style="color: #20a0ff">{{item.sum.state}}</span>套</div>
                </el-col>

                <div>
                    <v-chart class="echarts" :theme="chartsTheme" :options="item.data"/>
                </div>
            </el-row>
        </div>

        <div style="margin-top: 30px">
            <el-button @click="downloadData"
                       type="primary" size="mini">数据下载
            </el-button>
        </div>

        <el-table v-loading="houseLoading"
                  :data="houseList"
                  @sort-change="sortChange"
                  border
                  style="width: 100%;margin-top: 20px;margin-bottom: 15px;">
            <el-table-column prop="houseNo" label="房号"></el-table-column>
            <el-table-column sortable="custom" width="150" prop="useType"
                             label="房屋用途"></el-table-column>
            <el-table-column width="150" sortable="custom" prop="buildArea"
                             label="建筑面积"></el-table-column>
            <el-table-column width="150" sortable="custom" prop="indoorArea"
                             label="室内面积"></el-table-column>
            <el-table-column width="150" sortable="custom" prop="unitPrice"
                             label="单价"></el-table-column>
            <el-table-column width="150" sortable="custom" prop="sumPrice"
                             label="总价"></el-table-column>

        </el-table>

        <div v-if="houseList && houseList.length>0" class="block"
             style="float: right; margin: 10px 0;">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20, 30, 50]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next"
                    :total="totalNum">
            </el-pagination>
        </div>
        <a :href="xlsUrl" download id="downloadData" style="display: none" target="_blank"></a>
    </section>
</template>

<script>
    import ECharts from 'vue-echarts'
    import 'echarts/lib/chart/line'
    import 'echarts/lib/component/polar'
    import vintage from '../../common/js/vintage'

    export default {
        name: "PriceBureauHouse",
        components: {
            'v-chart': ECharts
        },
        data() {
            return {
                xlsUrl: '',
                houseList: [],
                project: {
                    id: 0,
                    projectName: '',
                    permitNum: '',
                    companyName: '',
                    address: ''
                },
                sortColumn: 'use_type',
                sortOrder: 'asc',
                projectId: 0,
                houseLoading: false,
                pageSize: 10,
                totalNum: 0,
                currentPage: 1,
                chartsData: [],
                chartsTheme: 'vintage',
                charts: {
                    title: {
                        subtext: '来自住建局'
                    },
                    tooltip: {
                        trigger: 'axis'
                    },
                    legend: {
                        data: ['备案价格']
                    },
                    toolbox: {
                        show: true,
                        feature: {
                            dataView: {show: true, readOnly: false},
                            magicType: {show: true, type: ['line', 'bar']},
                            restore: {show: true},
                            saveAsImage: {show: true}
                        }
                    },
                    calculable: true,
                    xAxis: [
                        {
                            type: 'category',
                            boundaryGap: true,
                            data: []

                        }
                    ],
                    yAxis: [
                        {
                            type: 'value',
                            axisLabel: {
                                formatter: '{value} 元/平米'
                            }
                        },
                        {
                            name: "建筑面积",
                            type: 'value',
                            axisLabel: {
                                formatter: '{value} 平米'
                            }
                        }
                    ],
                    series: [
                        {
                            name: '备案价格',
                            type: 'line',
                            xAxisIndex: 0,
                            yAxisIndex: 0,
                            data: []
                        }, {
                            name: '建筑面积',
                            type: 'bar',
                            xAxisIndex: 0,
                            yAxisIndex: 1,
                            data: []
                        }
                    ]
                },
            }
        },
        methods: {
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getHouseList();
            },
            downloadData() {

                var downloadLink = document.getElementById("downloadData");
                let token = sessionStorage.getItem("token");
                let userId = sessionStorage.getItem("userId");
                downloadLink.href = `${process.env.URL_SCHEME}${process.env.URL_HOST}/sz/web/amr/downloadData?projectId=` + this.project.id + "&projectName=" + this.project.projectName + "&token=" + token + "&userId=" + userId;
                downloadLink.click();
                this.$message({
                    type: 'success',
                    message: '下载成功',
                });
            },
            sortChange(ele) {
                if (ele.prop === 'useType') {
                    this.sortColumn = 'use_type';
                }
                if (ele.prop === 'buildArea') {
                    this.sortColumn = 'build_area';
                }
                if (ele.prop === 'indoorArea') {
                    this.sortColumn = 'indoor_area';
                }
                if (ele.prop === 'unitPrice') {
                    this.sortColumn = 'unit_price';
                }
                if (ele.prop === 'sumPrice') {
                    this.sortColumn = 'sum_price';
                }

                if (ele.order === 'descending') {
                    this.sortOrder = 'desc';
                } else {
                    this.sortOrder = 'asc';
                }
                this.currentPage = 1;
                this.getHouseList();
            },
            handleSizeChange(size) {
                this.currentPage = 1;
                this.pageSize = size;
                this.getHouseList()
            },
            getAveragePrice() {
                this.httpGet('/sz/web/amr/getAveragePrice', {
                    projectId: this.project.id
                }).then(data => {
                    if (data.code === 0) {
                        for (let i = 0; i < data.list.length; i++) {
                            let cData = JSON.parse(JSON.stringify(this.charts));
                            cData.series[0].data = data.list[i].unitPrice
                            cData.series[1].data = data.list[i].buildArea
                            cData.xAxis[0].data = data.list[i].name
                            this.chartsData.push({"sum": data.list[i].sum, "data": cData})
                        }
                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                })
            },
            getHouseList() {
                this.houseLoading = true;
                this.httpGet('/sz/web/amr/getHouse', {
                    projectId: this.project.id,
                    pageSize: this.pageSize,
                    sortOrder: this.sortOrder,
                    sortColumn: this.sortColumn,
                    curPage: this.currentPage,
                }).then(data => {
                    if (data.code === 0) {
                        this.houseList = data.feeds.list;
                        this.totalNum = data.feeds.totalItems;
                        this.houseLoading = false;
                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                })
            }
        },
        mounted() {
            this.getHouseList();
            this.getAveragePrice();
        },
        created() {
            this.project.id = this.$route.query.projectId;
            this.project.projectName = this.$route.query.projectName;
            this.project.permitNum = this.$route.query.permitNum;
            this.project.companyName = this.$route.query.companyName;
            this.project.address = this.$route.query.address;
        }
    }
</script>

<style scoped>
    .project-info {
        padding: 20px;
        margin: 20px 0;
        border: 1px solid #eee;
        border-left-color: #1b809e;
        border-left-width: 5px;
        border-radius: 3px;
        box-sizing: border-box;
    }

    .echarts {
        width: 100%;
        height: 500px;
    }
</style>


