import babelpolyfill from 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
//import './assets/theme/theme-green/index.css'
import VueRouter from 'vue-router'
import store from './vuex/store'
import Vuex from 'vuex'
import ECharts from 'vue-echarts' // refers to components/ECharts.vue in webpack

// import ECharts modules manually to reduce bundle size
import 'echarts/lib/chart/bar'
import 'echarts/lib/component/tooltip'

// If you want to use ECharts extensions, just import the extension package and it will work
// Taking ECharts-GL as an example:
// You only need to install the package with `npm install --save echarts-gl` and import it as follows
import 'echarts-gl'

import routes from './routes'
import 'font-awesome/css/font-awesome.min.css'
import 'video.js/dist/video-js.css'
import {kmovieGet, kmoviePost} from './api/http'
import axios from "axios/index";
import VueVideoPlayer from 'vue-video-player'


// register component to use
Vue.component('v-chart', ECharts)

Vue.use(VueVideoPlayer, /* {
  options: global default options,
  events: global videojs events
} */);
Vue.use(ElementUI);
Vue.use(VueRouter);
Vue.use(Vuex);

Vue.prototype.httpGet = kmovieGet;
Vue.prototype.httpPost = kmoviePost;

Vue.prototype.global = {
    // 图片上传接口
    uploadImgUrl: `${process.env.URL_SCHEME}${process.env.URL_HOST}/rest/n/oscar/app/file/mediaCloud/uploadImageToTaskId`
};
//NProgress.configure({ showSpinner: false });

const router = new VueRouter({
    routes
})

router.beforeEach((to, from, next) => {
    //NProgress.start();
    next()


    if (to.path === '/login') {
        sessionStorage.removeItem('uesrInfo');
    }
    let adminInfo = JSON.parse(sessionStorage.getItem('uesrInfo'));
    if (!adminInfo && to.path !== '/login' && to.path !== '/tip') {
        next({ path: '/login' })
    } else {
        next()
    }
})

//http response 登录拦截器
axios.interceptors.response.use(
    response => {
        if(response.data.code === 14){
            router.push({
                path:"/login",
                query:{redirect:router.currentRoute.fullPath}//从哪个页面跳转
            })
        }else if(response.data.code === 114){
            router.push({
                path:"/Tip",
                query:{redirect:router.currentRoute.fullPath}//从哪个页面跳转
            })
        }
        return response;
    },
    error => {
        return Promise.reject(error)
    }
);
//router.afterEach(transition => {
//NProgress.done();
//});

new Vue({
    //el: '#app',
    //template: '<App/>',
    router,
    store,
    //components: { App }
    render: h => h(App)
}).$mount('#app')





