<template>
    <section>

        <div style="margin: 15px 0;">
            <el-row>
                <el-radio-group @change="regionChange" v-model="curRegion">
                    <el-radio-button label="">全市</el-radio-button>
                    <el-radio-button label="宝安"></el-radio-button>
                    <el-radio-button label="福田"></el-radio-button>
                    <el-radio-button label="龙岗"></el-radio-button>
                    <el-radio-button label="罗湖"></el-radio-button>
                    <el-radio-button label="南山"></el-radio-button>
                    <el-radio-button label="盐田"></el-radio-button>
                    <el-radio-button label="深汕合作"></el-radio-button>
                </el-radio-group>
            </el-row>

            <el-row style="margin: 15px 0;display: flex;">
                <el-input class="input-search" v-model="key" placeholder='输入项目名'
                          @keyup.enter.native="getProjectList">
                    <i slot="prefix" class="el-input__icon el-icon-search"></i>
                    <el-button slot="append" icon="el-icon-search" @click="getProjectList">搜索</el-button>

                </el-input>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <el-button @click="getNewProject">获取新项目</el-button>
            </el-row>
        </div>

        <el-table v-loading="projectLoading"
                  :data="projectList"
                  border
                  style="width: 100%;margin-top: 20px;margin-bottom: 15px;">
            <el-table-column prop="name" label="项目名">
                <template slot-scope="scope">
                    <el-button type="text" @click="jumpProjectHouse(scope.row)">{{scope.row.name}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="count" width="100" label="预售总套数"></el-table-column>
            <el-table-column prop="usePeriod" width="150" label="产权年限"></el-table-column>
            <el-table-column width="200" prop="useType" label="房屋用途"></el-table-column>
            <el-table-column prop="region" width="100" label="区域"></el-table-column>
            <el-table-column prop="detailAddress" label="位置"></el-table-column>

        </el-table>
        <div v-if="projectList && projectList.length>0" class="block"
             style="float: right; margin: 10px 0;">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20, 30, 50]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next"
                    :total="totalNum">
            </el-pagination>
        </div>
    </section>
</template>

<script>
    export default {
        name: "NewProject",
        data() {
            return {
                curRegion:'',
                projectList: [],
                key: "",
                projectLoading: false,
                pageSize: 10,
                totalNum: 0,
                currentPage: 1,
            }
        },
        methods: {
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getProjectList();
            },
            getNewProject() {
                this.httpGet('/sz/crawl/pre/sell/catchNewProjectOpen', {
                    page:1
                }).then(data => {
                    if (data.code !== 14 && data.code !== 114 && data.code !== 0) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }else {
                        this.$message({
                            type: 'info',
                            message: '正在获取中...',
                        });
                    }
                })
            },
            handleSizeChange(size) {
                this.currentPage = 1;
                this.pageSize = size;
                this.getProjectList()
            },
            regionChange(region){
                this.curRegion=region;
                this.getProjectList();
            },
            jumpProjectHouse(row){
                this.$router.push({
                    path: '/SzNewProjectHouse',
                    query: {
                        projectId: row.id
                    }
                })
            },
            getProjectList() {
                this.projectLoading = true;
                this.httpGet('/sz/web/preSell/queryProject', {
                    key: this.key,
                    region: this.curRegion,
                    pageSize: this.pageSize,
                    curPage: this.currentPage,
                }).then(data => {
                    if (data.code === 0) {
                        this.projectList = data.feeds.list;
                        this.totalNum = data.feeds.totalItems;
                        this.projectLoading = false;
                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                })
            }
        },
        mounted() {
            this.getProjectList();
        }
    }
</script>

<style scoped>

</style>


