<template>
    <section>

        <div style="margin: 15px 0;">
            <el-row style="display: flex;">
                <el-input class="input-search" v-model="key" placeholder='项目名'
                          @keyup.enter.native="getProjectList">
                    <i slot="prefix" class="el-input__icon el-icon-search"></i>
                    <el-button slot="append" icon="el-icon-search" @click="getProjectList"></el-button>
                </el-input>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <el-button @click="getNewProject">获取新项目</el-button>
            </el-row>
        </div>

        <el-table v-loading="projectLoading"
                  :data="projectList"
                  border
                  style="width: 100%;margin-top: 20px;margin-bottom: 15px;">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="projectName" label="项目名">
                <template slot-scope="scope">
                    <el-button type="text" @click="jumpProjectHouse(scope.row)">{{scope.row.projectName}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="permitNum" label="销售许可证"></el-table-column>
            <el-table-column width="200" prop="companyName" label="开发商"></el-table-column>
            <el-table-column width="200" prop="address" label="位置"></el-table-column>

        </el-table>
        <div v-if="projectList && projectList.length>0" class="block"
             style="float: right; margin: 10px 0;">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20, 30, 50]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next"
                    :total="totalNum">
            </el-pagination>
        </div>
    </section>
</template>

<script>
    export default {
        name: "PriceBureauProject",
        data() {
            return {
                projectList: [],
                key: "",
                projectLoading: false,
                pageSize: 10,
                totalNum: 0,
                currentPage: 1,
            }
        },
        methods: {
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getProjectList();
            },
            handleSizeChange(size) {
                this.currentPage = 1;
                this.pageSize = size;
                this.getProjectList()
            },
            jumpProjectHouse(row){
                this.$router.push({
                    path: '/SzPriceBureauHouse',
                    query: {
                        projectId: row.id,
                        projectName: row.projectName,
                        permitNum: row.permitNum,
                        companyName: row.companyName,
                        address: row.address,
                    }
                })
            },

            getNewProject() {
                this.httpGet('/sz/crawl/amr/project/crawlHouse', {}).then(data => {
                    if (data.code !== 14 && data.code !== 114 && data.code !== 0) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }else {
                        this.$message({
                            type: 'info',
                            message: '正在获取中...',
                        });
                    }
                })
            },
            getProjectList() {
                this.projectLoading = true;
                this.httpGet('/sz/web/amr/queryProject', {
                    key: this.key,
                    pageSize: this.pageSize,
                    curPage: this.currentPage,
                }).then(data => {
                    if (data.code === 0) {
                        this.projectList = data.feeds.list;
                        this.totalNum = data.feeds.totalItems;
                        this.projectLoading = false;
                    } else if (data.code !== 14 && data.code !== 114) {
                        this.$message({
                            type: 'error',
                            message: '查询失败，联系' + process.env.DEVELOPER + "解决",
                        });
                    }
                })
            }
        },
        mounted() {
            this.getProjectList();
        }
    }
</script>

<style scoped>

</style>


